import React from 'react';
import PropTypes from 'prop-types';
import Card from '../cards/finder__results__card';
import BestBet from '../cards/finder__results__bestbet';
import DidYouMean from './finder__didyoumean';
import Summary from './finder__results__summary';
import Pagination from './finder__pagination';

/**
 * Render Funnelback results.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function finder__results(props) {
    const updating = (
        <p className="finder__results__updating" aria-live="polite">
            <span className="fas fa-spinner fa-pulse icon" aria-hidden="true" />{' '}
            <span>Updating {props.summariseAs.plural}</span>
        </p>
    );

    const resultsClass =
        'resultsVariant' in props.config
            ? `finder__results__list finder__results__list--${props.config.resultsVariant}`
            : 'finder__results__list';

    if (props.response) {
        const summary = (
            <Summary
                clear={props.clear}
                config={props.config}
                currEnd={props.response.summary.currEnd}
                currStart={props.response.summary.currStart}
                numRanks={props.response.summary.numRanks}
                query={props.query}
                summariseAs={props.summariseAs}
                totalMatching={props.response.summary.totalMatching}
                update={props.update}
            />
        );

        const didYouMean = (props.query.misspelling ||
            props.response.spell) && (
            <DidYouMean
                query={props.query}
                summariseAs={props.summariseAs}
                response={props.response}
                update={props.update}
            />
        );

        // if we have more results than will fit on a single page, we need
        // pagination
        const pagination = props.response.summary.totalMatching >
            props.response.summary.numRanks && (
            <Pagination
                currStart={props.response.summary.currStart}
                numRanks={props.response.summary.numRanks}
                query={props.query}
                totalMatching={props.response.summary.totalMatching}
                update={props.update}
            />
        );

        // render either the results, or a spinner while we wait for Funnelback
        const resultsContent = props.updating ? (
            <React.Fragment>{updating}</React.Fragment>
        ) : (
            <React.Fragment>
                {didYouMean}
                {summary}
                <ol
                    start={props.response.summary.currStart}
                    className={resultsClass}
                >
                    {props.response.bestBets.map((card) => (
                        <BestBet details={card} key={card.docNum} />
                    ))}
                    {props.response.results.map((card) => (
                        <Card
                            bestBet={false}
                            details={card}
                            key={card.docNum}
                            type={props.type}
                            query={props.query}
                        />
                    ))}
                </ol>
                {pagination}
            </React.Fragment>
        );
        return <div className="finder__results">{resultsContent}</div>;
    } else {
        return <div className="finder__results">{updating}</div>;
    }
}

finder__results.propTypes = {
    clear: PropTypes.func,
    query: PropTypes.object,
    response: PropTypes.object,
    summariseAs: PropTypes.object,
    type: PropTypes.string,
    update: PropTypes.object,
    updating: PropTypes.bool,
};

export default finder__results;
