import React from 'react';
import PropTypes from 'prop-types';

/**
 * Render a Funnelback result as a module card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function finder__results__module(props) {
    const location = props.details.metaData.location && (
            <p className="finder__results__card__tag">
                <span
                    className="fas fa-fw fa-map-marker-alt icon"
                    aria-hidden="true"
                />{' '}
                <span>Location:</span>{' '}
                <span>{props.details.metaData.location}</span>
            </p>
        ),
        title = props.details.metaData.code ? (
            <React.Fragment>
                <span className="finder__results__card--module__code">
                    {props.details.metaData.code}
                </span>{' '}
                <span>{props.details.title}</span>
            </React.Fragment>
        ) : (
            <React.Fragment>
                <span>{props.details.title}</span>
            </React.Fragment>
        ),
        school = props.details.metaData.school && (
            <p className="finder__results__card__tag">
                <span
                    className="fas fa-fw fa-university icon"
                    aria-hidden="true"
                />{' '}
                <span>School:</span>{' '}
                <span>
                    {props.details.metaData.school.split('|').length > 2
                        ? props.details.metaData.school
                              .split('|')
                              .slice(0, -1)
                              .join(', ') +
                          ', and ' +
                          props.details.metaData.school.split('|').slice(-1)
                        : props.details.metaData.school
                              .split('|')
                              .join(', and ')}
                </span>
            </p>
        ),
        term = props.details.metaData.term && (
            <p className="finder__results__card__tag">
                <span
                    className="far fa-fw fa-calendar icon"
                    aria-hidden="true"
                />{' '}
                <span>Semester:</span>{' '}
                <span>
                    {props.details.metaData.term.split('|').length > 2
                        ? 'All year'
                        : props.details.metaData.term.split('|').join(' or ')}
                </span>
            </p>
        ),
        topic = props.details.metaData.related && (
            <p className="finder__results__card__description">
                {props.details.metaData.related
                    .split('|')
                    .sort()
                    .map((topic, i) => (i > 0 ? topic.toLowerCase() : topic))
                    .join(', ')}
            </p>
        ),
        specification = props.details.metaData.specification && (
            <p className="finder__results__card__tag">
                <span className="far fa-fw fa-file-pdf icon" aria-label="PDF" />{' '}
                <a href={props.details.metaData.specification}>
                    Download module specification
                </a>
            </p>
        );

    return (
        <li className="finder__results__card finder__results__card--module">
            <div className="finder__results__card__details">
                <h3 className="card__title finder__results__card__heading">
                    {title}
                </h3>
                {topic}
                <p>{props.details.metaData.c}</p>
                {term}
                {school}
                {location}
                {specification}
            </div>
        </li>
    );
}

finder__results__module.propTypes = {
    details: PropTypes.object,
    query: PropTypes.object,
};

export default finder__results__module;
