import React from 'react';
import PropTypes from 'prop-types';
import { formatReactDate } from '../../../../util';

/**
 * Render a Funnelback result as a generic card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function finder__results__news(props) {
    const formattedDate = formatReactDate(new Date(props.details.metaData.d)),
        dateString = props.details.metaData.d && (
            <p className="finder__results__card__description">
                {formattedDate}
            </p>
        ),
        hashtags = props.details.metaData.hashtagtext && (
            <p className="finder__results__card__tag">
                <span
                    className="fad fa-fw fa-hashtag icon"
                    aria-hidden="true"
                />{' '}
                <span>
                    Hashtag
                    {props.details.metaData.hashtagtext.split('|').length > 1 &&
                        's'}
                    :
                </span>{' '}
                <span>
                    {props.details.metaData.hashtagtext.split('|').join(', ')}
                </span>
            </p>
        );

    return (
        <li className="finder__results__card finder__results__card--news">
            <a href={props.details.clickTrackingUrl}>
                <div className="finder__results__card__details">
                    <h3 className="card__title finder__results__card__heading">
                        {props.details.title}
                    </h3>
                    {dateString}
                    <p>
                        {props.details.metaData.c && props.details.metaData.c}
                    </p>
                    {hashtags}
                </div>
            </a>
        </li>
    );
}

finder__results__news.propTypes = {
    details: PropTypes.object,
};

export default finder__results__news;
