import React from 'react';
import PropTypes from 'prop-types';

/**
 * Render a Funnelback result as a contact card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function finder__results__contact(props) {
    const department = props.details.metaData.department && (
            <p className="finder__results__card__tag">
                <span
                    className="fas fa-fw fa-building icon"
                    aria-hidden="true"
                />{' '}
                <span>Department:</span> {props.details.metaData.department}
            </p>
        ),
        jobtitle = props.details.metaData.jobtitle && (
            <p className="finder__results__card__description">
                {props.details.metaData.jobtitle}
            </p>
        ),
        email = props.details.metaData.email && (
            <p className="finder__results__card__tag">
                <span
                    className="fas fa-fw fa-envelope icon"
                    aria-hidden="true"
                />{' '}
                <span>Email:</span>{' '}
                <a href={'mailto:' + props.details.metaData.email}>
                    {props.details.metaData.email}
                </a>
            </p>
        ),
        telephone = props.details.metaData.telephone && (
            <p className="finder__results__card__tag">
                <span
                    className="fas fa-fw fa-phone fa-rotate-90 icon"
                    aria-hidden="true"
                />{' '}
                <span>Telephone:</span>{' '}
                <a href={'tel:' + props.details.metaData.telephone}>
                    {props.details.metaData.friendlytelephone}
                </a>
            </p>
        ),
        room = props.details.metaData.room && (
            <p className="finder__results__card__tag">
                <span
                    className="fas fa-fw fa-door-open icon"
                    aria-hidden="true"
                />{' '}
                <span>Room:</span> {props.details.metaData.room}
            </p>
        );

    return (
        <li className="finder__results__card finder__results__card--contact">
            <div className="finder__results__card__details">
                <h3 className="card__title finder__results__card__heading">
                    {props.details.title}
                </h3>
                {jobtitle}
                {department}
                {room}
                {email}
                {telephone}
            </div>
        </li>
    );
}

finder__results__contact.propTypes = {
    details: PropTypes.object,
};

export default finder__results__contact;
