import React from 'react';
import PropTypes from 'prop-types';
import SelectFilter from './finder__select';
import CheckboxFilter from './finder__checkbox';
import TagFilter from './finder__tag';
import Reset from './finder__reset';
import Sort from './finder__sort';
import { gaEvent } from '../../../../util';

/**
 * Predicate for whether a facet should be displayed, if it's dependent on
 * another facet being set.
 *
 * @param {object} facet The facet to potentially display.
 * @param {object} facetMap The facets currently set on the query.
 * @return {bool} - Has any dependency been met
 */
function dependencyMet(facet, facetMap) {
    const setFacets = Object.keys(facetMap);

    if (!facet.dependency) {
        return true;
    }

    if (
        setFacets.indexOf(facet.dependency) >= 0 &&
        facetMap[facet.dependency] !== ''
    ) {
        return true;
    }

    return false;
}

/**
 * Component to update facet values in the query.
 *
 * @param {object} props React props.
 * @returns {object} - React component.
 */
function finder__filters(props) {
    const clearFiltersDesktop =
            Object.keys(props.query.facets).length > 0 ? (
                <div className="finder__filters__reset finder__filters__reset--desktop">
                    <Reset clear={props.clear} resetSort={false} />
                </div>
            ) : null,
        clearFiltersMobile =
            Object.keys(props.query.facets).length > 0 ||
            props.query.sortType !== props.config.sort[0].type ? (
                <Reset clear={props.clear} resetSort={true} />
            ) : null;

    const sort =
        props.config.sort.length > 1 && props.config.displaySort ? (
            <div className="wrapper--finder__sort--mobile">
                <Sort
                    config={props.config}
                    query={props.query}
                    update={props.update}
                />
            </div>
        ) : null;

    return (
        <div className="finder__filters">
            <h2 className="finder__filters__heading">
                <span className="finder__filters__heading__text">
                    <span
                        className="far fa-sliders-h icon"
                        aria-hidden="true"
                    />{' '}
                    {`Filter ${props.config.summariseAs.plural}`}
                </span>
                {clearFiltersMobile}
            </h2>

            <fieldset>
                <div className="finder__filters__wrapper--filters">
                    {props.config.facetLabels.map((facet) => {
                        if (dependencyMet(facet, props.query.facets)) {
                            switch (facet.type) {
                                case 'select':
                                    return (
                                        <SelectFilter
                                            key={facet.meta}
                                            facet={facet}
                                            query={props.query}
                                            responseFacet={
                                                props.response &&
                                                props.response.facets
                                                    ? props.response.facets.filter(
                                                          (funnelbackFacet) =>
                                                              funnelbackFacet.name ===
                                                              facet.funnelbackName
                                                      )
                                                    : []
                                            }
                                            update={props.update}
                                            dependencies={props.config.facetLabels.filter(
                                                (candidate) =>
                                                    candidate.dependency ===
                                                    facet.meta
                                            )}
                                        />
                                    );
                                case 'checkbox':
                                    return (
                                        <CheckboxFilter
                                            key={facet.meta}
                                            facet={facet}
                                            query={props.query}
                                            responseFacet={
                                                props.response &&
                                                props.response.facets
                                                    ? props.response.facets.filter(
                                                          (funnelbackFacet) =>
                                                              funnelbackFacet.name ===
                                                              facet.funnelbackName
                                                      )
                                                    : []
                                            }
                                            update={props.update}
                                            dependencies={props.config.facetLabels.filter(
                                                (candidate) =>
                                                    candidate.dependency ===
                                                    facet.meta
                                            )}
                                        />
                                    );
                                case 'tag':
                                    return (
                                        <TagFilter
                                            key={facet.meta}
                                            facet={facet}
                                            query={props.query}
                                            responseFacet={
                                                props.response &&
                                                props.response.facets
                                                    ? props.response.facets.filter(
                                                          (funnelbackFacet) =>
                                                              funnelbackFacet.name ===
                                                              facet.funnelbackName
                                                      )
                                                    : []
                                            }
                                            update={props.update}
                                            dependencies={props.config.facetLabels.filter(
                                                (candidate) =>
                                                    candidate.dependency ===
                                                    facet.meta
                                            )}
                                        />
                                    );
                                default:
                                    gaEvent(
                                        'jsError',
                                        'JavaScript error',
                                        'finder__filters()',
                                        'Unknown filter type in finder__filters.js',
                                        true
                                    );
                            }
                        } else {
                            return null;
                        }
                    })}
                    <p className="finder__filters__nofilters">
                        No filters are valid for the current query.
                    </p>
                    {clearFiltersDesktop}
                    {sort}
                </div>
            </fieldset>
        </div>
    );
}

finder__filters.propTypes = {
    config: PropTypes.object,
    query: PropTypes.object,
    response: PropTypes.object,
    update: PropTypes.object,
    clear: PropTypes.func,
};

export default finder__filters;
