import React from 'react';
import { render } from 'react-dom';
import Finder from './patterns/finder/finder';
import { finderConfig } from './patterns/finder/funnelback';

document.addEventListener('DOMContentLoaded', () => {
    if (/(Trident|MSIE)/.test(navigator.userAgent)) {
        return;
    } else {
        const finders = document.querySelectorAll('.wrapper--finder');
        finders &&
            Array.from(finders).forEach(finder => {
                finderConfig(finder.dataset.config).then(config => {
                    render(<Finder config={config} element={finder} />, finder);
                });
            });
    }
});
