import React from 'react';
import PropTypes from 'prop-types';

/**
 * Component to change the sort order of the query.
 *
 * @param {object} props React props.
 * @returns {object} - React component.
 */
function finder__sort(props) {
    const stringLength = 16,
        stringOffset = -4,
        randomNumber = Math.random().toString(stringLength).slice(stringOffset);

    const setSort = (type) => {
        const newQuery = props.query;

        newQuery.sortType = type;
        newQuery.startRank = 1;
        newQuery.misspelling = null;
        newQuery.interacted = true;

        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    const validSorts = props.config.sort.filter((sortType) =>
        sortType.type === ''
            ? props.query.query !== '' ||
              Object.keys(props.query.facets).length > 0 ||
              props.query.sortType === ''
                ? true
                : false
            : true
    );

    return validSorts.length > 1 ? (
        <div
            className={`finder__sort finder__select${
                props.query.sortType !== props.config.sort[0].type
                    ? ' finder__select--selected'
                    : ''
            }`}
        >
            <label
                className="finder__select__overline"
                htmlFor={`sort--${randomNumber}`}
            >
                Sort by
            </label>
            <select
                name="sort"
                id={`sort--${randomNumber}`}
                onChange={(e) => setSort(e.target.value)}
                value={props.query.sortType}
            >
                {validSorts.map((sortType, i) => {
                    return (
                        <option key={i} value={sortType.type}>
                            {sortType.label}
                        </option>
                    );
                })}
            </select>
        </div>
    ) : null;
}

finder__sort.propTypes = {
    config: PropTypes.object,
    query: PropTypes.object,
    update: PropTypes.object,
};

export default finder__sort;
