import React from 'react';
import PropTypes from 'prop-types';

function finder__tag(props) {
    const deleteFacet = () => {
        const newQuery = props.query;

        props.dependencies.map((facet) => {
            delete newQuery.facets[facet.meta];
        });

        delete newQuery.facets[props.facet.meta];

        newQuery.startRank = 1;
        newQuery.misspelling = null;
        newQuery.interacted = true;

        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    return props.query.facets[props.facet.meta] ? (
        <div className="finder__filter finder__tag">
            <button onClick={() => deleteFacet()} type="button">
                <span
                    className="fa-fw far fa-times icon"
                    aria-hidden="true"
                ></span>
                <span className="finder__tag__text">
                    <span className="sr-only">Remove filter for </span>
                    {props.facet.name}:{' '}
                    {
                        props.facet.values.filter(
                            (value) =>
                                value.data ===
                                props.query.facets[props.facet.meta]
                        )[0].label
                    }
                </span>
            </button>
        </div>
    ) : null;
}

finder__tag.propTypes = {
    facet: PropTypes.object,
    query: PropTypes.object,
    responseFacet: PropTypes.arrayOf(PropTypes.object),
    update: PropTypes.object,
    dependencies: PropTypes.arrayOf(PropTypes.object),
};

export default finder__tag;
