import React from 'react';
import PropTypes from 'prop-types';
import { uppercaseFirstLetterLowercaseRest } from '../../../../util';

/**
 * Render a Funnelback result as an accommodation card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function finder__results__accommodation(props) {
    const mph = 60,
        calculateTime = (minutes) =>
            Math.floor(minutes / mph) > 0
                ? Math.floor(minutes / mph) + ' hour ' + (minutes % mph)
                : minutes,
        thumbnail = props.details.metaData.thumbnail && (
            <img src={props.details.metaData.thumbnail} alt="" />
        ),
        level = props.details.metaData.level && (
            <p className="finder__results__card__tag">
                <span
                    className="fas fa-fw fa-graduation-cap icon"
                    aria-hidden="true"
                />{' '}
                <span className="sr-only">Level:</span>{' '}
                <span>
                    {uppercaseFirstLetterLowercaseRest(
                        props.details.metaData.level
                    )
                        .split('|')
                        .join(' or ')}
                </span>
            </p>
        ),
        features = Object.keys(props.details.metaData)
            .filter(
                (meta) =>
                    ['accessible', 'catered', 'ensuite'].includes(meta) &&
                    props.details.metaData[meta] === 'Yes'
            )
            .map((meta) => meta.replace(/ensuite/, 'en suite'))
            .sort()
            .join(', '),
        type = props.details.metaData.type && (
            <p className="finder__results__card__tag">
                <span
                    className="fas fa-fw fa-building icon"
                    aria-hidden="true"
                />{' '}
                <span className="sr-only">Type:</span>{' '}
                <span>
                    {props.details.metaData.type.split('|').length > 2
                        ? uppercaseFirstLetterLowercaseRest(
                              props.details.metaData.type
                          )
                              .split('|')
                              .slice(0, -1)
                              .join(', ') +
                          ' or ' +
                          uppercaseFirstLetterLowercaseRest(
                              props.details.metaData.type
                          )
                              .split('|')
                              .slice(-1)
                        : uppercaseFirstLetterLowercaseRest(
                              props.details.metaData.type
                          )
                              .split('|')
                              .join(' or ')}
                    {features && ' (' + features + ')'}
                </span>
            </p>
        ),
        price = props.details.metaData.price && (
            <p className="finder__results__card__tag">
                <span
                    className="fas fa-fw fa-pound-sign icon"
                    aria-hidden="true"
                />{' '}
                <span className="sr-only">Price:</span>{' '}
                <span>
                    {props.details.metaData.price} (
                    {props.details.metaData.deposit} deposit)
                </span>
            </p>
        ),
        transport = props.details.metaData.transport && (
            <React.Fragment>
                {' '}
                ({calculateTime(props.details.metaData.transport)} minutes by
                public transport)
            </React.Fragment>
        ),
        distance = props.details.metaData.walk && (
            <p className="finder__results__card__tag">
                <span
                    className="fas fa-fw fa-walking icon"
                    aria-hidden="true"
                />{' '}
                <span className="sr-only">
                    Distance to Northampton Square campus:
                </span>{' '}
                <span>
                    {calculateTime(props.details.metaData.walk)} minutes walk to
                    campus
                    {transport}
                </span>
            </p>
        );

    return (
        <li className="finder__results__card finder__results__card--accommodation">
            <a href={props.details.clickTrackingUrl}>
                {thumbnail}
                <div className="finder__results__card__details">
                    <h3 className="card__title finder__results__card__heading">
                        {props.details.title}
                    </h3>
                    {distance}
                    {type}
                    {price}
                    {level}
                </div>
            </a>
        </li>
    );
}

finder__results__accommodation.propTypes = {
    details: PropTypes.object,
};

export default finder__results__accommodation;
